/* jshint esversion: 6 */ 

import { isUndefined } from 'util';

export {
    returnFirstArgument,
    sumWithDefaults,
    returnArgumentsArray,
    returnFnResult,
    returnCounter,
    bindFunction 
};

/*  1 -  */

/*
  1:

 1.1:       
 1.2:    ,     

 :
   returnFirstArgument(10)  10
   returnFirstArgument('')  ``

  :       ,     
 */
function returnFirstArgument(a) {
    return a;
}

/*
  2:

 2.1:      

 :
   sumWithDefaults(10, 20)  30
   sumWithDefaults(2, 4)  6

 2.1 *:          100

 :
   sumWithDefaults(10)  110
 */
function sumWithDefaults(a, b = 100) {
    return a + b;
}

/*
  3:

           

 :
   returnFnResult(() => '')  ''
 */
function returnFnResult(fn) {
    return fn();
}

/*
  4:

         (F)
    F,            F

 :
   var f = returnCounter(10);

   console.log(f()); //  11
   console.log(f()); //  12
   console.log(f()); //  13
 */
function returnCounter(number) { 
  
    if (isUndefined(number)) {
        number = 0;
    } 

    return function F() {
        return ++number;
    } ;
}

/*
  5 *:

          
     

 :
   returnArgumentsArray(1, 2, 3)  [1, 2, 3]
 */
function returnArgumentsArray() {
    var arr = [];

    for ( let i = 0; i < arguments.length; i++) {
        arr[i] = arguments[i];
    }

    return arr;
}

/*
  6 *:

      (F)     
        F    

 :
   function sum(a, b) {
     return a + b;
   }

   var newSum = bindFunction(sum, 2, 4);

   console.log(newSum())  6
 */
function bindFunction(F, ...args) {

/*    var arr = [];
    var str = '';

    for ( let i = 1; i < arguments.length; i++) {
        arr[i-1] = arguments[i];
    }
    str = arr.join(', ');
*/
    F = F.bind(null, ...args);

    return F;
} 


